/***************************************************
    APP BASE STYLES
 ***************************************************/
html {
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: var(--body-font-size);
  color: #333;
}

#main-wrapper {
  flex-grow: 1;
}

::selection {
  color: #fff;
  background: dodgerblue;
}

figure {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

video {
  display: block;
}

.body-loading {
  overflow: hidden;
}

.body-loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 999999999;
}
.body-loader.fadeout {
  animation: fadeLoaderOut 0.2s 0.2s linear forwards;
}

@keyframes fadeLoaderOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
/***************************************************
    GLOBAL TYPOGRAPHY
    (Available even a mixin to centralize typography
    rules, not globally but only when needed, in
    global/mixins.scss)
 ***************************************************/
p {
  margin-bottom: 2rem;
  line-height: 1.7;
}
p a {
  text-decoration: underline;
}

a {
  color: dodgerblue;
  text-decoration: none;
  transition: color 0.3s;
}
a:hover {
  color: #000;
}

h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
  font-weight: 700;
  font-size: inherit;
}

.section-title {
  font-weight: 700;
  font-size: 4rem;
  line-height: 1.2;
}
.section-title > * {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
}
.section-title.big {
  font-size: 4.8rem;
}

/***************************************************
    UTILITIES
 ***************************************************/
img.image-fit {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
img.image-fit.absolute {
  position: absolute;
  top: 0;
  left: 0;
}

/***************************************************
    BUTTONS
 ***************************************************/
.button {
  display: inline-block;
  min-width: 120px;
  padding: 0.6rem 2rem;
  font-family: "Roboto", sans-serif;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  color: #fff;
  background: dodgerblue;
  border: none;
  border-radius: 0;
  transition: background 0.3s, transform 0.3s;
  outline: none;
}
.button:hover, .button:focus {
  color: #fff;
  background: rgba(30, 144, 255, 0.6);
  transform: translateY(-4px);
}
.button.white {
  color: white;
  background: transparent;
  border: 1px solid currentColor;
}
.button.white:hover, .button.white:focus {
  background: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  cursor: not-allowed;
  background: rgba(30, 144, 255, 0.6);
  transform: none;
}

/******************************************
    SITE SLIDERS
 ******************************************/
.site-carousel img {
  width: 100%;
}
.site-carousel .swiper-button-prev::after,
.site-carousel .swiper-button-next::after {
  font-family: "Font Awesome 5 Pro";
  font-weight: 900;
  font-size: 30px;
  color: dodgerblue;
}
.site-carousel .swiper-button-prev {
  left: 20px;
}
.site-carousel .swiper-button-prev::after {
  content: "\f060";
}
.site-carousel .swiper-button-next {
  right: 20px;
}
.site-carousel .swiper-button-next::after {
  content: "\f061";
}
.site-carousel .swiper-pagination-bullets .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  background: transparent;
  border: 1px solid dodgerblue;
  opacity: 1;
  outline: none;
}
.site-carousel .swiper-pagination-bullets .swiper-pagination-bullet-active {
  background: dodgerblue;
}

/******************************************************
    CONTACT FORM
******************************************************/
.wpcf7-form {
  font-size: 1.6rem;
}
.wpcf7-form .wpcf7-form-control-wrap {
  display: block;
  margin-bottom: 1.75rem;
}
.wpcf7-form input[type=text],
.wpcf7-form input[type=email],
.wpcf7-form input[type=number],
.wpcf7-form input[type=tel],
.wpcf7-form textarea {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 1rem;
  color: #333;
  background: #ccc;
  border: none;
  border-radius: 0;
  outline: none;
  transition: background 0.3s;
}
.wpcf7-form input[type=text]:hover, .wpcf7-form input[type=text]:focus,
.wpcf7-form input[type=email]:hover,
.wpcf7-form input[type=email]:focus,
.wpcf7-form input[type=number]:hover,
.wpcf7-form input[type=number]:focus,
.wpcf7-form input[type=tel]:hover,
.wpcf7-form input[type=tel]:focus,
