.wpcf7-form textarea:focus {
  background: #bfbfbf;
}
.wpcf7-form textarea {
  height: 100px;
  padding: 1rem;
}
.wpcf7-form ::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
  opacity: 1; /* Firefox */
}
.wpcf7-form .legenda {
  margin-bottom: 0.5rem;
}
.wpcf7-form .wrap-acceptance .wpcf7-list-item {
  margin: 0;
}
.wpcf7-form .wrap-acceptance label {
  position: relative;
  text-transform: none;
  cursor: pointer;
}
.wpcf7-form .wrap-acceptance label input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -1;
}
.wpcf7-form .wrap-acceptance label input + span {
  position: relative;
  padding-left: 2rem;
  display: block;
}
.wpcf7-form .wrap-acceptance label input + span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 12px;
  height: 12px;
  border: 1px solid currentColor;
}
.wpcf7-form .wrap-acceptance label input + span::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 6px;
  height: 6px;
  background: dodgerblue;
  opacity: 0;
  transition: opacity 0.2s;
}
.wpcf7-form .wrap-acceptance label input:checked + span::after {
  opacity: 1;
}
.wpcf7-form .wrap-acceptance label input:focus + span::before {
  outline: 2px dotted currentColor;
  outline-offset: 3px;
}
.wpcf7-form .wrap-acceptance label a {
  color: inherit;
}
.wpcf7-form .wrap-submit {
  position: relative;
  text-align: right;
}
.wpcf7-form .wrap-submit .wpcf7-spinner {
  position: absolute;
  top: 48px;
  right: 16px;
  margin: 0;
  background: #4e545a;
}

.wpcf7-response-output,
.wpcf7-not-valid-tip,
.wpcf7 form.invalid .wpcf7-response-output,
.wpcf7 form.unaccepted .wpcf7-response-output {
  padding: 0.25rem 0.5rem;
  color: #fff;
  border: none;
  background: #e63746;
}

.wpcf7 form.failed .wpcf7-response-output,
.wpcf7 form.sent .wpcf7-response-output,
.wpcf7 form.invalid .wpcf7-response-output,
.wpcf7 form.unaccepted .wpcf7-response-output {
  margin: 1rem 0 0;
}

.wpcf7 form.sent .wpcf7-response-output {
  color: #fff;
  border: 0;
  background: rgba(0, 128, 0, 0.65);
}

