/******************************************************
    INTERNAL PAGES (template standard) and 404
 ******************************************************/

.wrap-internals {
    padding-top: 8rem;
    padding-bottom: 8rem;

    h1 {
        margin-bottom: 3rem;
    }

    .wrap-content {
        h2,
        h3,
        h4 {
            color: $primary;
        }

        h2 {
            font-size: 2.4rem;
            margin-bottom: 2rem;
        }

        h3 {
            font-size: 2rem;
            margin-bottom: 1.5rem;
        }

        h4 {
            font-size: 1.8rem;
            margin-bottom: 1rem;
        }
    }
}

#page-404 {
    .image {
        margin-bottom: 4rem;
        width: 200px;
    }
}
